.card {
  background: #fff;
  border-radius: 0;
  height: 325px;
  margin: 1rem;
  position: relative;
  z-index: 7;
  width: 325px;
  box-shadow: 0 0px 37px #27ff00, 0 0px 11px #0077ff;
  text-align: left;
}

.card > .img-container {
  height: 100%;
  /*overflow: hidden;*/
  text-align: center;
  
  background: -webkit-repeating-linear-gradient(
  45deg,
  #2c2e30,
  #2c2e30 10px,
  #202223 10px,
  #202223 20px);
  
  background: repeating-linear-gradient(
  45deg,
  #2c2e30,
  #2c2e30 10px,
  #202223 10px,
  #202223 20px);
  background-color:#202223;

}
.card > .img-container > span > img {
  width: 100%
}

.card > .content {
  padding-left: 1rem;
  padding-right: 1rem;
  font-size: 15px;
}

.card > .content > ul {
  list-style-type: none;
}

.card > .content > ul > li {
  margin-top: 18px;
}

.card > .remove {
  color: #ff1744;
  font-size: 48px;
  position: absolute;
  top: -20px;
  right: 5px;
  cursor: pointer;
}

.card > .remove:hover {
  color: #d50000;
}

.wiggle_and {
    -webkit-animation: shake 0.3s;
            animation: shake 0.3s;
    -webkit-animation-iteration-count: 1;
            animation-iteration-count: 1;
}

@-webkit-keyframes shake {
    0% { -webkit-transform: translate(5px, 0px); transform: translate(5px, 0px); }
    10% { -webkit-transform: translate(-5px, 0px); transform: translate(-5px, 0px); }
    20% { -webkit-transform: translate(5px, 0px); transform: translate(5px, 0px); }
    30% { -webkit-transform: translate(-5px, 0px); transform: translate(-5px, 0px); }
    40% { -webkit-transform: translate(5px, 0px); transform: translate(5px, 0px); }
    50% { -webkit-transform: translate(-5px, 0px); transform: translate(-5px, 0px); }
    60% { -webkit-transform: translate(5px, 0px); transform: translate(5px, 0px); }
    70% { -webkit-transform: translate(-5px, 0px); transform: translate(-5px, 0px); }
    80% { -webkit-transform: translate(5px, 0px); transform: translate(5px, 0px); }
    90% { -webkit-transform: translate(-5px, 0px); transform: translate(-5px, 0px); }
    100% { -webkit-transform: translate(5px, 0px); transform: translate(5px, 0px); }
}

@keyframes shake {
    0% { -webkit-transform: translate(5px, 0px); transform: translate(5px, 0px); }
    10% { -webkit-transform: translate(-5px, 0px); transform: translate(-5px, 0px); }
    20% { -webkit-transform: translate(5px, 0px); transform: translate(5px, 0px); }
    30% { -webkit-transform: translate(-5px, 0px); transform: translate(-5px, 0px); }
    40% { -webkit-transform: translate(5px, 0px); transform: translate(5px, 0px); }
    50% { -webkit-transform: translate(-5px, 0px); transform: translate(-5px, 0px); }
    60% { -webkit-transform: translate(5px, 0px); transform: translate(5px, 0px); }
    70% { -webkit-transform: translate(-5px, 0px); transform: translate(-5px, 0px); }
    80% { -webkit-transform: translate(5px, 0px); transform: translate(5px, 0px); }
    90% { -webkit-transform: translate(-5px, 0px); transform: translate(-5px, 0px); }
    100% { -webkit-transform: translate(5px, 0px); transform: translate(5px, 0px); }
}
