.logo {
  background-image: url(/clicky-game/static/media/Ben_logo.8f190106.png);
  background-repeat: no-repeat;
  width: 100px;
  height: 100px;
  background-size: contain;
  filter: drop-shadow(0px 0px 7px green);
  -webkit-filter: drop-shadow(0px 0px 7px green);
  -moz-filter: drop-shadow(0px 0px 7px green);
  -o-filter: drop-shadow(0px 0px 7px green);
  -ms-filter: drop-shadow(0px 0px 7px green);
  -webkit-animation: pulse;
          animation: pulse;
  -webkit-animation-iteration-count: 10;
          animation-iteration-count: 10;
}

@-webkit-keyframes pulse {
  0% { -webkit-filter: drop-shadow(0px 0px 7px rgb(200, 255, 200)); filter: drop-shadow(0px 0px 7px rgb(200, 255, 200)); }
  25% { -webkit-filter: drop-shadow(0px 0px 11px rgb(109, 226, 109)); filter: drop-shadow(0px 0px 11px rgb(109, 226, 109)); }
  50% { -webkit-filter: drop-shadow(0px 0px 13px green); filter: drop-shadow(0px 0px 13px green); }
  75% { -webkit-filter: drop-shadow(0px 0px 15px rgb(1, 82, 1)); filter: drop-shadow(0px 0px 15px rgb(1, 82, 1)); }
  100% { -webkit-filter: drop-shadow(0px 0px 17px rgb(1, 27, 1)); filter: drop-shadow(0px 0px 17px rgb(1, 27, 1)); }
}

@keyframes pulse {
  0% { -webkit-filter: drop-shadow(0px 0px 7px rgb(200, 255, 200)); filter: drop-shadow(0px 0px 7px rgb(200, 255, 200)); }
  25% { -webkit-filter: drop-shadow(0px 0px 11px rgb(109, 226, 109)); filter: drop-shadow(0px 0px 11px rgb(109, 226, 109)); }
  50% { -webkit-filter: drop-shadow(0px 0px 13px green); filter: drop-shadow(0px 0px 13px green); }
  75% { -webkit-filter: drop-shadow(0px 0px 15px rgb(1, 82, 1)); filter: drop-shadow(0px 0px 15px rgb(1, 82, 1)); }
  100% { -webkit-filter: drop-shadow(0px 0px 17px rgb(1, 27, 1)); filter: drop-shadow(0px 0px 17px rgb(1, 27, 1)); }
}
